
<template>
  <div style="background-color: #F6F6F6;position:fixed;top:50px;bottom:0;left:0;right:0;overflow-y:scroll;">
    <div>
      <!-- 生活用水 -->
      <div
        class="box_1"
        v-if="life_unit != 0"
      >
        <p class="life">
          生活用水&ensp;
          <span class="unit">单价:&yen;{{ life_unit }}/吨</span>
        </p>
        <div class="last">
          <p class="num">上次读数</p>
          <p>
            {{ life_1?life_1:'0' }} 吨
            <span
              class="time"
              v-if="lase_time"
            ></span>
          </p>
        </div>
        <div class="box_1_1">
          <mt-field
            label="本次读数"
            type="number"
            placeholder="请输入"
            @blur.native.capture="btn_life"
            v-model="life_2"
          ></mt-field>
          <div class="dun">
            吨
          </div>
        </div>
        <div class="last">
          <p class="num">本次应收</p>
          <p style="color:#FB9E58;font-size:16px;">
            {{ life_totle }}
            <span class="time">元</span>
          </p>
        </div>
      </div>
      <!-- 电费 -->
      <div
        class="box_1"
        v-if="ele_unit != 0"
      >
        <p class="life">
          电费&ensp;
          <span class="unit">单价:&yen;{{ ele_unit }}/度</span>
        </p>
        <div class="last">
          <p class="num">上次读数</p>
          <p>
            {{ ele_1?ele_1:'0' }} 度
            <span
              class="time"
              v-if="lase_time"
            ></span>
          </p>
        </div>
        <div class="box_1_1">
          <mt-field
            label="本次读数"
            type="number"
            placeholder="请输入"
            @blur.native.capture="btn_ele"
            v-model="ele_2"
          ></mt-field>
          <div class="dun">
            度
          </div>
        </div>
        <div class="last">
          <p class="num">本次应收</p>
          <p style="color:#FB9E58;font-size:16px;">
            {{ ele_totle }}
            <span class="time">元</span>
          </p>
        </div>
      </div>
      <!-- 气费 -->
      <div
        class="box_1"
        v-if="gas_unit != 0"
      >
        <p class="life">
          气费&ensp;
          <span class="unit">单价:&yen;{{ gas_unit }}/度</span>
        </p>
        <div class="last">
          <p class="num">上次读数</p>
          <p>
            {{ gas_1?gas_1:'0' }} 度
            <span
              class="time"
              v-if="lase_time"
            ></span>
          </p>
        </div>
        <div class="box_1_1">
          <mt-field
            label="本次读数"
            type="number"
            placeholder="请输入"
            @blur.native.capture="btn_gas"
            v-model="gas_2"
          ></mt-field>
          <div class="dun">
            度
          </div>
        </div>
        <div class="last">
          <p class="num">本次应收</p>
          <p style="color:#FB9E58;font-size:16px;">
            {{ gas_totle }}
            <span class="time">元</span>
          </p>
        </div>
      </div>
      <!-- 其他费用 -->
      <div class="box_1">
        <p class="life">其他费用</p>
        <mt-field
          label="清洁费(元/月)"
          type="number"
          placeholder="请输入"
          class="other"
          v-model="cleanPrice"
        ></mt-field>
        <mt-field
          label="停车费(元/月)"
          type="number"
          placeholder="请输入"
          class="other"
          v-model="partPrice"
        ></mt-field>
        <mt-field
          label="物业费(元/月)"
          type="number"
          placeholder="请输入"
          class="other last_other"
          v-model="propPrice"
        ></mt-field>
      </div>
      <!-- 换房记录 -->
      <div style="margin:20px 0 60px;">
        <div v-if="recordRoom.length > 0">
          <div
            class="box_1"
            v-for="(item,i) in recordRoom"
            :key="i"
          >
            <p class="life">
              换房结算&emsp;&emsp;&emsp;&ensp;
              <span>{{item.preRoomSn}}➜{{item.roomSn}}</span>
            </p>
            <div
              class="last cost"
              v-if="life_unit != 0"
            >
              <p>水费结算</p>
              <p>{{item.water_totalPrice}}元</p>
              <p>起始：{{item.preWaterDegree?item.preWaterDegree:0}}</p>
              <p>结算：{{item.waterDegree?item.waterDegree:0}}</p>
            </div>
            <div
              class="last cost"
              v-if="ele_unit != 0"
            >
              <p>电费结算</p>
              <p>{{item.ele_totalPrice}}元</p>
              <p>起始：{{item.preElecDegree?item.preElecDegree:0}}</p>
              <p>结算：{{item.elecDegree?item.elecDegree:0}}</p>
            </div>
            <div
              class="last cost"
              v-if="gas_unit != 0"
            >
              <p>气费结算</p>
              <p>{{item.gas_totalPrice}}元</p>
              <p>起始：{{item.preGasDegree?item.preGasDegree:'0'}}</p>
              <p>结算：{{item.gasDegree?item.gasDegree:'0'}}</p>
            </div>
          </div>
        </div>
        <!-- 换表记录 -->
        <div v-if="recordWatch.length > 0">
          <div
            class="box_1"
            v-for="(item,i) in recordWatch"
            :key="i"
          >
            <p class="life">
              换表结算&emsp;&emsp;&emsp;&ensp;
            </p>
            <div
              class="last cost"
              v-if="item.meterType == 1"
            >
              <p>电费结算</p>
              <p>{{item.ele_totalPrice}}元</p>
              <p>起始：{{item.preElecDegree?item.preElecDegree:0}}</p>
              <p>结算：{{item.elecDegree?item.elecDegree:0}}</p>
            </div>
            <div
              class="last cost"
              v-if="item.meterType == 2"
            >
              <p>水费结算</p>
              <p>{{item.water_totalPrice}}元</p>
              <p>起始：{{item.preWaterDegree?item.preWaterDegree:0}}</p>
              <p>结算：{{item.waterDegree?item.waterDegree:0}}</p>
            </div>
            <div
              class="last cost"
              v-if="item.meterType == 3"
            >
              <p>气费结算</p>
              <p>{{item.gas_totalPrice}}元</p>
              <p>起始：{{item.preGasDegree?item.preGasDegree:'0'}}</p>
              <p>结算：{{item.gasDegree?item.gasDegree:'0'}}</p>
            </div>
          </div>
        </div>
      </div>
      <div class="bottom">
        <div
          class="go"
          @click="commit"
        >保存修改</div>
      </div>
    </div>
    <!-- 撤回 -->
    <mt-popup
      v-model="back_box"
      style="width:80%;border-radius:10px;"
    >
      <div class="edit_box">
        <div class="isBack">是否撤回该账单？</div>
        <div class="back_bottom">
          <p
            class="first_canal"
            @click="back_cancel"
          >取消</p>
          <p
            class="first_ok"
            @click="back_ok"
          >确定</p>
        </div>
      </div>
    </mt-popup>
  </div>
</template>
<script>
import { Toast, MessageBox } from 'mint-ui';
import { GetBusinessVirtualRoomscale, GetBusinessLifeCostRoomRecordByLifeCostBill, GetBusinessLifeCostBillLastByorderroom, PostBusinessLifeCostBillRecall } from '@/api/api_business_hotel_and_scale';
import cookie from '@/common/cookie.js';
export default {
    data() {
        return {
            life_fee: '',
            life_unit: '',
            ele_unit: '',
            life_1: '0',
            life_2: '',
            ele_1: '0',
            ele_2: '',
            cleanPrice: '',
            partPrice: '',
            propPrice: '',
            lase_time: '',
            startDate: new Date(),
            dataVal1: new Date(),
            gas_unit: '',
            gas_1: '',
            gas_2: '',
            both: '',
            back_box: false,
            back_show: '',
            recordRoom: [],
            recordWatch: [],
            dataForm: {
                life_woter: ''
            }
        };
    },
    watch: {
        dataForm: {
            handler: function(newVal) {
                window.sessionStorage.setItem('lifeFreeData', JSON.stringify(this.dataForm));
            },
            deep: true
        }
    },
    computed: {
        life_totle: function() {
            return ((this.life_2 - this.life_1) * this.life_unit).toFixed(2) < 0 ? 0 : ((this.life_2 - this.life_1) * this.life_unit).toFixed(2);
        },
        ele_totle: function() {
            return ((this.ele_2 - this.ele_1) * this.ele_unit).toFixed(2) < 0 ? 0 : ((this.ele_2 - this.ele_1) * this.ele_unit).toFixed(2);
        },
        gas_totle: function() {
            return ((this.gas_2 - this.gas_1) * this.gas_unit).toFixed(2) < 0 ? 0 : ((this.gas_2 - this.gas_1) * this.gas_unit).toFixed(2);
        },
        totlePrice: function() {
            return Number(this.life_totle) + Number(this.ele_totle) + Number(this.gas_totle) + Number(this.cleanPrice) + Number(this.partPrice) + Number(this.propPrice);
        }
    },
    created() {
        window.sessionStorage.getItem('lifeFreeData') ? (this.dataForm = JSON.parse(window.sessionStorage.getItem('lifeFreeData'))) : '';
        if (this.$route.query.num) {
            this.orderRoomId = JSON.parse(localStorage.getItem('arrive')).id;
            this.roomScaleId = JSON.parse(localStorage.getItem('arrive')).virtualRoomScaleId;
            this.$store.commit('setHeader', '新建生活账单');
            this.getUnit();
            this.lastList();
        } else {
            this.$store.commit('setHeader', '生活账单详情');
            this.life_fee = this.$route.query.life_fee;
            this.life_1 = this.life_fee.preWaterDegree;
            this.life_2 = this.life_fee.waterDegree;
            this.life_unit = this.life_fee.waterPrice;
            this.ele_1 = this.life_fee.preElecDegree;
            this.ele_2 = this.life_fee.elecDegree;
            this.ele_unit = this.life_fee.elecPrice;
            this.gas_1 = this.life_fee.preGasDegree;
            this.gas_2 = this.life_fee.gasDegree;
            this.gas_unit = this.life_fee.gasPrice;
            this.propPrice = this.life_fee.propPrice;
            this.partPrice = this.life_fee.parkPrice;
            this.cleanPrice = this.life_fee.rubbishPrice;
            this.both = this.life_fee.payStatus == 1 ? '已支付' : '线下现金支付';
            this.back_show = this.life_fee.payStatus == 1 ? false : true;
        }
        this.getRecord();
    },
    methods: {
        getRecord() {
            GetBusinessLifeCostRoomRecordByLifeCostBill(this.life_fee.id).done((res) => {
                this.recordRoom = res.data.filter((item) => item.meterType == 0);
                this.recordRoom.forEach((element) => {
                    element.ele_totalPrice = (element.elecDegree - element.preElecDegree) * this.ele_unit;
                    element.water_totalPrice = (element.waterDegree - element.preWaterDegree) * this.life_unit;
                    element.gas_totalPrice = (element.gasDegree - element.preGasDegree) * this.gas_unit;
                });
                this.recordWatch = res.data.filter((item) => item.meterType != 0);
                this.recordWatch.forEach((item) => {
                    item.ele_totalPrice = (item.elecDegree - item.preElecDegree) * this.ele_unit;
                    item.water_totalPrice = (item.waterDegree - item.preWaterDegree) * this.life_unit;
                    item.gas_totalPrice = (item.gasDegree - item.preGasDegree) * this.gas_unit;
                });
            });
        },
        getDateStr(AddDayCount) {
            var dd = new Date();
            dd.setDate(dd.getDate() + AddDayCount); //获取AddDayCount天后的日期
            var y = dd.getFullYear();
            var m = dd.getMonth() + 1; //获取当前月份的日期
            var d = dd.getDate();
            return y + '-' + (m < 10 ? '0' + m : m) + '-' + (d < 10 ? '0' + d : d);
        },
        // 查询上次账单
        lastList() {
            GetBusinessLifeCostBillLastByorderroom(this.orderRoomId).done((res) => {
                if (this.$route.query.num) {
                    this.life_1 = res.data.waterDegree;
                    this.ele_1 = res.data.elecDegree;
                }
            });
        },
        // 查询单价
        getUnit() {
            GetBusinessVirtualRoomscale(this.roomScaleId).done((res) => {
                this.ele_unit = res.data.elecPrice;
                this.life_unit = res.data.waterPrice;
            });
        },
        // 重置水费
        cancle_life() {
            this.life_2 = '';
        },
        // 重置电费
        cancle_ele() {
            this.ele_2 = '';
        },
        // 重置气费
        cancle_gas() {
            this.gas_2 = '';
        },
        // 开启选择时间
        dateClick() {
            this.$refs.picker1.open();
        },
        // 取消
        Cancel() {
            this.$refs.picker1.close();
        },
        btn_life() {
            if (this.life_2 < this.life_1 || !/^[0-9]*$/.test(this.life_2)) {
                Toast({
                    message: '请正确输入本次用水读数',
                    duration: 900
                });
                this.life_2 = '';
                return false;
            }
        },
        btn_ele() {
            if (this.ele_2 < this.ele_1 || !/^[0-9]*$/.test(this.ele_2)) {
                Toast({
                    message: '请正确输入本次电费读数',
                    duration: 900
                });
                this.ele_2 = '';
                return false;
            }
        },
        btn_gas() {
            if (this.gas_2 < this.gas_1 || !/^[0-9]*$/.test(this.gas_2)) {
                Toast({
                    message: '请正确输入本次气费读数',
                    duration: 900
                });
                this.gas_2 = '';
                return false;
            }
        },
        // 提交账单
        commit() {
            if (this.life_unit != 0 && !this.life_2) {
                Toast({
                    message: '请正确输入本次用水读数',
                    duration: 900
                });
                this.life_2 = '';
                return false;
            }
            if (this.ele_unit != 0 && !this.ele_2) {
                Toast({
                    message: '请正确输入本次电费读数',
                    duration: 900
                });
                this.ele_2 = '';
                return false;
            }
            if (this.gas_unit != 0 && !this.gas_2) {
                Toast({
                    message: '请正确输入本次气费读数',
                    duration: 900
                });
                this.gas_2 = '';
                return false;
            }
            if (this.$route.query.num) {
                let hotelId = cookie.getCookie('currentID');
                let params = {
                    orderRoomId: this.orderRoomId,
                    hotelId: hotelId,
                    waterPrice: this.life_unit,
                    waterDegree: this.life_2,
                    preWaterDegree: this.life_1 == '0' ? '0' : this.life_1.toFixed(2),
                    elecPrice: this.ele_unit,
                    elecDegree: this.ele_2,
                    preElecDegree: this.ele_1 == '0' ? '0' : this.ele_1.toFixed(2),
                    propPrice: this.propPrice,
                    rubbishPrice: this.cleanPrice,
                    parkPrice: this.partPrice
                };
            } else {
                this.lifeInfo = {};
                this.lifeInfo.waterDegree = this.life_2;
                this.lifeInfo.elecDegree = this.ele_2;
                this.lifeInfo.gasDegree = this.gas_2;
                this.lifeInfo.propPrice = this.propPrice;
                this.lifeInfo.rubbishPrice = this.cleanPrice;
                this.lifeInfo.parkPrice = this.partPrice;
                localStorage.setItem('life_fee', JSON.stringify(this.lifeInfo));
                setTimeout('window.history.go(-1)', 900);
            }
        },
        // 撤回
        back(item) {
            MessageBox.confirm('', {
                message: '是否撤回账单？',
                title: '提示',
                cancelButtonText: '否',
                confirmButtonText: '是'
            })
                .then((action) => {
                    if (action == 'confirm') {
                        PostBusinessLifeCostBillRecall(item.id).done((res) => {
                            this.back_box = false;
                            setTimeout('window.history.go(-1)', 900);
                        });
                    }
                })
                .catch((e) => {});
        },
        back_cancel() {
            this.back_box = false;
        },
        // 撤回
        back_ok() {
            PostBusinessLifeCostBillRecall(this.back_id).done((res) => {
                this.back_box = false;
                setTimeout('window.history.go(-1)', 900);
            });
        }
    }
};
</script>
<style scoped>
.box_1 {
    margin-top: 15px;
    background: #fff;
    color: #000;
    font-size: 14px;
    padding: 0 10px;
    text-align: left;
    line-height: 35px;
}
.life {
    font-weight: bold;
    border-bottom: 1px solid #e5e5e5;
}
.unit {
    font-size: 12px;
    color: #b2b2b2;
    font-weight: normal;
}
.last {
    padding: 0 10px;
    display: flex;
    border-bottom: 1px solid #e5e5e5;
}
.cost {
    justify-content: space-between;
}
.cost > p:nth-child(3) {
    color: #888;
    font-size: 0.6rem;
    opacity: 0.6;
}
.cost > p:nth-child(4) {
    color: #888;
    font-size: 0.6rem;
    opacity: 0.6;
}
.num {
    width: 32%;
}
.time {
    color: #b2b2b2;
}
/deep/ .mint-cell-text {
    font-size: 14px;
}
/deep/ .mint-field-core {
    font-size: 14px;
}
.box_1_1 {
    display: flex;
    border-bottom: 1px solid #e5e5e5;
}
.dun {
    display: flex;
    align-items: center;
}
.other {
    border-bottom: 1px solid #e5e5e5;
}
.etime {
    margin-top: 15px;
    line-height: 26px;
    display: flex;
    justify-content: space-between;
    font-size: 14px;
    padding: 10px 20px;
    background: #fff;
    margin-bottom: 65px;
}
.bottom {
    display: flex;
    justify-content: space-between;
    width: 100%;
    font-size: 14px;
    line-height: 50px;
    position: fixed;
    bottom: 0;
}
.totle {
    width: 50%;
    background-color: #fff;
    font-weight: bold;
    line-height: 20px;
    padding-top: 8px;
}
.pay {
    background-color: #02bb6c;
    color: #fff;
    width: 50%;
}
.back {
    background-color: #6a6a6a;
    color: #fff;
    width: 50%;
}
.go {
    background-color: #fb9e58;
    color: #fff;
    width: 100%;
}
.bottom_box {
    height: 40px;
    font-size: 0.9rem;
    margin-top: 50px;
    line-height: 40px;
}
.last_box {
    background: #fb9e58;
    color: #fff;
}
.isBack {
    line-height: 40px;
    font-size: 0.8rem;
}
.back_bottom {
    font-size: 0.8rem;
    display: flex;
    line-height: 30px;
}
.first_canal {
    width: 50%;
    border: 1px solid #cecece;
    border-bottom-left-radius: 10px;
}
.first_ok {
    color: #fb9e58;
    border: 1px solid #cecece;
    width: 50%;
    border-bottom-right-radius: 10px;
}
</style>



